export const showLogin = (login) => ({
  type: 'SHOW_LOGIN',
  login,
})

export const showEdit = (edit) => ({
  type: 'SHOW_EDIT',
  edit,
})

export const showSidebar = (sidebar) => ({
  type: 'SHOW_SIDEBAR',
  sidebar,
})

export const showSearchResult = (search) => ({
  type: 'SHOW_SEARCH_RESULT',
  search,
})
